/* eslint-disable no-param-reassign */
/* eslint-disable no-unused-vars */
/* eslint-disable react/no-array-index-key */
import React, { useRef, useState, useEffect, useCallback } from 'react'
import { useInView } from 'react-intersection-observer'
import { IconButton, Typography } from '@material-ui/core'
import SwiperCore, { Pagination, Navigation } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react'
import FeedCard from '../../../components/Sub/FeedCard/FeedCard'
import ToolPanel from '../../../components/Sub/ToolPanel/ToolPanel'
import LoadingOverlay from '../../../components/Sub/DataTable/LoadingOverlay/LoadingOverlay'
import useStyles from './FeedPage.style'
import api from '../../../api/api'
import useAsync from '../../../hooks/useAsync'
import {
  API_REPORTS_FEED_ENDPOINT,
  API_USER_REPORTS_ENDPOINT,
  API_REPORT_BY_ID_ENDPOINT,
} from '../../../utils/constants'
import { NewReportPopup } from '../../../components/Sub/GlobalPopups'
import FoundJobPopup from '../../../components/Sub/GlobalPopups/FoundJobPopup/FoundJobPopup'

// import Swiper core and required modules
SwiperCore.use([Pagination, Navigation])

const FeedPage = ({ setSnack }) => {
  const [reports, setReports] = React.useState(null)
  const navigationPrevRef = React.useRef(null)
  const navigationNextRef = React.useRef(null)
  const loader = useRef(null)
  const [myReports, setMyReports] = React.useState({ results: [] })
  const [isEditOpen, setIsEditOpen] = useState(false)
  const [reportToEdit, setReportToEdit] = useState({})
  const [isSwiper, setIsSwiper] = useState(false)
  const [isJobModalOpen, setIsJobModalOpen] = useState(false)
  const classes = useStyles()

  const { run: runMyReports, isLoading: isMyReportLoading } = useAsync({
    data: [],
    error: null,
  })

  const { run: runReports, isLoading: isReportsLoading } = useAsync({
    data: [],
    error: null,
  })

  const onDeleteCard = async (id) => {
    try {
      const response = await api.delete(API_REPORT_BY_ID_ENDPOINT(id))
      const newReports = myReports.results.filter((c) => c.id !== id)
      setMyReports({ ...myReports, results: newReports })

      return Promise.resolve(newReports)
    } catch (e) {
      return Promise.reject(e.response)
    }
  }

  const getReports = async (params = '?limit=3') => {
    try {
      const response = await api.get(API_REPORTS_FEED_ENDPOINT(params))
      if (params === '?limit=3') {
        setReports(response.data)
      } else {
        setReports({ ...response.data, results: [...reports.results, ...response.data.results] })
      }
      return Promise.resolve(response.data)
    } catch (e) {
      return Promise.reject(e.response)
    }
  }
  const getMyReports = async () => {
    try {
      const response = await api.get(API_USER_REPORTS_ENDPOINT('limit=100'))
      setMyReports(response.data)
      return Promise.resolve(response.data)
    } catch (e) {
      return Promise.reject(e.response)
    }
  }

  React.useEffect(() => {
    runReports(getReports())
    runMyReports(getMyReports())
  }, [])

  const { ref, inView, entry } = useInView({
    threshold: 0.5,
  })

  React.useEffect(() => {
    if (myReports.results.length < 4) {
      setIsSwiper(false)
    } else {
      setIsSwiper(true)
    }
  }, [myReports.results])

  useEffect(() => {
    if (reports && reports.next) {
      let reportsNext = reports?.next ?? ''
      reportsNext = reports.next.split('feed')
      reportsNext = reportsNext[reportsNext.length - 1]
      if (inView) runReports(getReports(reportsNext))
    }
  }, [inView])

  const handleUpdateReports = () => {
    runMyReports(getMyReports())
  }

  return (
    <div className={classes.root}>
      <NewReportPopup
        open={isEditOpen}
        setOpen={setIsEditOpen}
        setSnack={setSnack}
        currentReport={reportToEdit}
        refreshFeeds={handleUpdateReports}
        openFoundJobPopup={() => setIsJobModalOpen(true)}
      />
      <FoundJobPopup openState={[isJobModalOpen, setIsJobModalOpen]} setSnack={setSnack} />
      <div className={classes.topContainer}>
        <Typography variant="h3" component="h1" className={classes.title}>
          Мои записи
        </Typography>
        <ToolPanel setSnack={setSnack} refreshFeeds={handleUpdateReports} />
      </div>

      {myReports.results.length !== 0 && (
        <div className={classes.slider}>
          {isMyReportLoading && <LoadingOverlay loading={isMyReportLoading} />}
          {myReports && myReports.results.length !== 0 && myReports.results.length > 3 && (
            <IconButton className={`${classes.swiperNavPrev}`} ref={navigationPrevRef}>
              <svg width="8" height="15" viewBox="0 0 8 15" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path
                  d="M7.57738 1.9346C7.57732 2.1998 7.47193 2.45411 7.28438 2.6416L2.69838 7.2276C2.51589 7.41773 2.41399 7.67106 2.41399 7.9346C2.41399 8.19814 2.51589 8.45147 2.69838 8.6416L7.28438 13.2276C7.46654 13.4162 7.56733 13.6688 7.56506 13.931C7.56278 14.1932 7.45761 14.444 7.2722 14.6294C7.08679 14.8148 6.83598 14.92 6.57378 14.9223C6.31159 14.9246 6.05898 14.8238 5.87038 14.6416L1.28438 10.0556C0.738909 9.48398 0.43457 8.72422 0.43457 7.9341C0.43457 7.14398 0.738908 6.38422 1.28438 5.8126L5.87038 1.2276C6.01023 1.08779 6.1884 0.992585 6.38235 0.954018C6.5763 0.91545 6.77734 0.935254 6.96004 1.01092C7.14274 1.08659 7.2989 1.21473 7.40878 1.37914C7.51867 1.54355 7.57734 1.73685 7.57738 1.9346Z"
                  fill="#1A1B22"
                />
              </svg>
            </IconButton>
          )}

          {!isSwiper ? (
            <div className={classes.cardContainer}>
              {myReports &&
                myReports.results.length !== 0 &&
                myReports.results.map((report, i) => (
                  <FeedCard
                    onDeleteCard={onDeleteCard}
                    isUser
                    key={i}
                    report={report}
                    onEdit={() => {
                      setReportToEdit(report)
                      setIsEditOpen(true)
                    }}
                  />
                ))}
            </div>
          ) : (
            <Swiper
              slidesPerView={3}
              spaceBetween={21}
              navigation={{
                prevEl: navigationPrevRef.current,
                nextEl: navigationNextRef.current,
              }}
              onBeforeInit={(swiper) => {
                swiper.params.navigation.prevEl = navigationPrevRef.current
                swiper.params.navigation.nextEl = navigationNextRef.current
              }}
            >
              {myReports &&
                myReports.results.map((report, i) => (
                  <SwiperSlide key={report.id}>
                    <FeedCard
                      isUser
                      onDeleteCard={onDeleteCard}
                      onEdit={() => {
                        setReportToEdit(report)
                        setIsEditOpen(true)
                      }}
                      id={report.id}
                      key={report.id}
                      report={report}
                    />
                  </SwiperSlide>
                ))}
            </Swiper>
          )}
          {myReports && myReports.results.length !== 0 && myReports.results.length > 3 && (
            <IconButton className={`${classes.swiperNavNext}`} ref={navigationNextRef}>
              <svg
                transform="rotate(180)"
                width="8"
                height="15"
                viewBox="0 0 8 15"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M7.57738 1.9346C7.57732 2.1998 7.47193 2.45411 7.28438 2.6416L2.69838 7.2276C2.51589 7.41773 2.41399 7.67106 2.41399 7.9346C2.41399 8.19814 2.51589 8.45147 2.69838 8.6416L7.28438 13.2276C7.46654 13.4162 7.56733 13.6688 7.56506 13.931C7.56278 14.1932 7.45761 14.444 7.2722 14.6294C7.08679 14.8148 6.83598 14.92 6.57378 14.9223C6.31159 14.9246 6.05898 14.8238 5.87038 14.6416L1.28438 10.0556C0.738909 9.48398 0.43457 8.72422 0.43457 7.9341C0.43457 7.14398 0.738908 6.38422 1.28438 5.8126L5.87038 1.2276C6.01023 1.08779 6.1884 0.992585 6.38235 0.954018C6.5763 0.91545 6.77734 0.935254 6.96004 1.01092C7.14274 1.08659 7.2989 1.21473 7.40878 1.37914C7.51867 1.54355 7.57734 1.73685 7.57738 1.9346Z"
                  fill="#1A1B22"
                />
              </svg>
            </IconButton>
          )}
        </div>
      )}

      <div>
        <Typography variant="h3" component="h2" className={classes.titleStudents}>
          Записи других студентов
        </Typography>
        {reports &&
          reports.results.length > 0 &&
          reports.results.map((report, index) => {
            if (reports.results.length === index + 1) {
              return <FeedCard ref={ref} id={report.id} key={report.id} profile={report.profile} report={report} />
            }
            return <FeedCard id={report.id} key={report.id} profile={report.profile} report={report} />
          })}
      </div>
    </div>
  )
}

export default FeedPage
